import * as CodeBlockMain from "react-code-blocks";

const CodeBloc = ({ code, language, theme, showLineNumbers, startingLineNumber })=> {
  return (
    <CodeBlockMain.CodeBlock
      text={code}
      language={language}
      showLineNumbers={showLineNumbers}
      startingLineNumber={startingLineNumber}
      theme={CodeBlockMain[ theme || 'dracula']}
    />
  );
}
export default CodeBloc