import React, { useRef, useEffect } from 'react';
import { Typography, Grid, Box } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import colors from '../common/colors'
import { Connect, mapDispatchToProps, mapStateToProps } from '../../redux';
import { Api } from '../../functions';
const useStyles = makeStyles((theme) => ({
    main: {
        height: '80vh',
        backgroundColor: colors.primary,
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        flexDirection: 'column'
    },
    number: {
        fontSize: 100
    },
    title: {
        fontSize: 30
    }
}));

const Customers = (props) => {
    const classes = useStyles(),
        { User, usersNumber, setByField } = props,
        timer = useRef(),

        getUsers = () => {
            Api({
                module: 'Users',
                data: {
                    userId: User._id,
                    action: 'getUsersTotal'
                }
            }).then(number => setByField('usersNumber', number))
        }
    useEffect(() => {
        getUsers();
        timer.current = setInterval(() => {
            getUsers()
        }, 2000)
        return () => {
            clearInterval(timer.current);
        }
    }, [])
    return (
        <Grid className={classes.main}>
            <Box>
                <Typography className={classes.number}>{usersNumber.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</Typography>
            </Box>
            <Typography className={classes.title}>Benkiko Users</Typography>

        </Grid>
    )
}

export default Connect(mapStateToProps, mapDispatchToProps)(Customers)


