
import React, { useState, Fragment } from 'react';
import Button from '@material-ui/core/Button';
import Avatar from '@material-ui/core/Avatar';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import ListItemText from '@material-ui/core/ListItemText';
import Dialog from '@material-ui/core/Dialog';
import PersonIcon from '@material-ui/icons/Person';
import AddIcon from '@material-ui/icons/Add';
import useStyles from '../Disputes/styles'
import { Box, InputAdornment, makeStyles, TextField } from '@material-ui/core';
import SearchIcon from '@material-ui/icons/Search'
import CloseIcon from '@material-ui/icons/Close';
import { Api } from '../../functions';
import Notify from "../Notifications";

const useStyles0 = makeStyles((theme) => ({
    actionBtns: {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'space-between'
    },
    btn: {
        textTransform: 'none'
    },
    form: {
        width: 500,
        padding: 15
    }
}));
const SimpleDialog = (props) => {
    const classes = useStyles(),
        classes0 = useStyles0(),
        { onClose, selectedValue, open, Users, User, fetchUsers } = props,
        [state, setState] = useState({
            searchVal: '',
            addAgent: false,
            Username: ''
        }),
        { searchVal, addAgent, Username } = state,
        handleClose = () => {
            onClose(selectedValue);
        },
        onSubmit = (e) => {
            e.preventDefault()
            let obj = {},
                data = new FormData(e.target);
            data.forEach((value, key) => obj[key] = value)
            handleListItemClick(obj, true)
        },
        handleListItemClick = (value, add) => {
            if (value === 'addAccount')
                setState({ addAgent: true })
            else
                Api({
                    module: 'Support',
                    data: {
                        action: 'addRemoveAgent',
                        userId: User._id,
                        agentId: value,
                        add
                    }
                }).then(({ message, status }) => {
                    Notify(message, status)
                    if (status === 'success') {
                        fetchUsers()
                        setState({ ...state, Username: '' })
                    }
                }).catch(e => Notify('Operation not successful', 'danger'))
        };

    return (
        <Dialog onClose={handleClose} aria-labelledby="simple-dialog-title" open={open}>
            <Box className={classes.headerView} style={{ margin: 0, padding: 0 }}>
                <h5 style={{ fontSize: 20, marginLeft: 10 }}>Reserve Agents</h5>
                <Button onClick={onClose} className={classes.createBtn}>
                    <CloseIcon style={{ color: 'grey', fontSize: 15 }} />
                </Button>
            </Box>
            {
                addAgent ?
                    <form className={classes0.form} onSubmit={onSubmit}>
                        <TextField
                            variant="outlined"
                            margin="normal"
                            value={Username}
                            onChange={(e) => setState({ ...state, Username: e.target.value })}
                            required
                            required
                            fullWidth
                            name="Username"
                            label="Username or Email"
                            id="Username"
                        />
                        <Box className={classes0.actionBtns} >
                            {
                                ['Cancel', 'Add agent'].map((button, index) => (
                                    <Button type={index === 0 ? 'button' : 'submit'} className={classes0.btn} key={index} onClick={() => {
                                        if (index === 0)
                                            setState({ addAgent: false })
                                    }} color="primary"> {button} </Button>
                                ))
                            }
                        </Box>
                    </form> :
                    <Fragment>
                        <Box className={classes.searchBox} style={{ width: '80%', marginLeft: 10, marginRight: 100 }}>
                            <TextField
                                placeholder={'Search'}
                                className={classes.searchInput}
                                classes={{ notchedOutline: classes.input }}
                                onChange={(c) => setState({ ...state, searchVal: c.target.value })}
                                InputProps={{
                                    disableUnderline: true,
                                    startAdornment: (
                                        <InputAdornment position="start">
                                            <SearchIcon style={{ color: '#8C8B8B', marginLeft: 10, marginRight: 10, fontSize: 16 }} />
                                        </InputAdornment>
                                    ),
                                    endAdornment: (
                                        <Button position="end" onClick={() => setState({ ...state, searchVal: '', users: [] })}>
                                            <CloseIcon style={{ color: '#8C8B8B', marginLeft: 10, marginRight: 10, fontSize: 16 }} />
                                        </Button>
                                    ),
                                    classes: { notchedOutline: classes.noBorder }
                                }}
                            />
                        </Box>
                        <List  >
                            {Users.filter(a => a._id !== 0 && (!searchVal || (searchVal &&
                                (a.Email.toLowerCase().indexOf(searchVal.toLowerCase()) !== -1 ||
                                    a.Username.toLowerCase().indexOf(searchVal.toLowerCase()) !== -1
                                )))).map(({ Image, _id, Username, Email }) => (
                                    <ListItem key={_id}>
                                        <ListItemAvatar>
                                            <Avatar className={classes.avatar}>
                                                {
                                                    Image ?
                                                        <img src={Image} className={classes.userImg} /> :
                                                        <PersonIcon />
                                                }
                                            </Avatar>
                                        </ListItemAvatar>
                                        <ListItemText primary={Username} secondary={Email} />
                                        <Button onClick={() => handleListItemClick(_id, false)} className={classes.removeAgent} item>Remove</Button>
                                    </ListItem>
                                ))}

                            <ListItem autoFocus button onClick={() => handleListItemClick('addAccount', null)}>
                                <ListItemAvatar>
                                    <Avatar>
                                        <AddIcon />
                                    </Avatar>
                                </ListItemAvatar>
                                <ListItemText primary="Add reserve agent" />
                            </ListItem>
                        </List>
                    </Fragment>
            }
        </Dialog>
    );
}
export default SimpleDialog