import React, { useState, useEffect, Fragment } from 'react'
import { Api, getDateTime } from '../../functions'
import { Connect, mapStateToProps } from '../../redux'
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import useStyles from '../Disputes/styles'
import { Box, Button, InputAdornment, TextField } from '@material-ui/core';
import SearchIcon from '@material-ui/icons/Search'
import CloseIcon from '@material-ui/icons/Close';
import Prompt from '../common/Prompt';
import AddIcon from '@material-ui/icons/Add';
import Form from './form';
import Notify from "../Notifications";

const columns = [
    { id: 'date', label: 'Date', minWidth: 170 },
    { id: 'title', label: 'Title', minWidth: 170 },
    { id: 'shortMessage', label: 'Short Message', minWidth: 200 },
    { id: 'url', label: 'Url', minWidth: 170 },
    {
        id: 'action',
        label: 'Action',
        sublabel: 'text',
        minWidth: 120,
        align: 'center'
    }
];

const Index = (props) => {
    const { User, history } = props,
        // history = useHistory(),
        [state, setState] = useState({
            news: [],
            searchVal: '',
            openPrompt: false,
            options: {
                title: '',
                message: ''
            },
            selected: {
                status: '',
                _id: ''
            },
            showForm: false
        }),
        { news, searchVal, showForm, openPrompt, options, selected } = state,
        fetchNews = () => {
            Api({
                module: 'Support',
                data: {
                    action: 'fetchNews',
                    userId: User._id
                }
            }).then(news => setState({ ...state, news })).catch(e => console.log(e))
        },
        classes = useStyles(),
        propmpOption = (option) => {
            if (option === "Yes")
                Api({
                    module: 'Support',
                    data: {
                        action: 'removeNews',
                        userId: User._id,
                        newsId: selected._id
                    }
                }).then(() => {
                    setState({ ...state, news: news.filter(a => a._id !== selected._id), openPrompt: false, selected: {} })
                    Notify('News has been deleted successfully.', 'success')
                }).catch(() => Notify('News could not be deleted.', 'danger'))
            setState({ ...state, openPrompt: false, selected: {} });
        },
        createNews = (e) => {
            e.preventDefault()
            let data = {
                action: 'createNews',
                userId: User._id,
            },
                formData = new FormData(e.target);
            formData.forEach((value, key) => data[key] = value);
            Api({
                module: 'Support',
                data
            }).then(news => {
                Notify('News has been broadcasted successfully.', 'success')
                setState({ ...state, showForm: false })
            }
            ).catch(e => Notify('Failed to broadcast news.', 'danger'))
        }
    useEffect(() => {
        fetchNews()
    }, [])
    return (
        <Paper className={classes.root}>
            <Box className={classes.headerView}>
                <Box className={classes.searchBox}>
                    <TextField
                        placeholder={'Search news'}
                        className={classes.searchInput}
                        value={searchVal}
                        classes={{ notchedOutline: classes.input }}
                        onChange={(c) => setState({ ...state, searchVal: c.target.value })}
                        InputProps={{
                            disableUnderline: true,
                            startAdornment: (
                                <InputAdornment position="start">
                                    <SearchIcon style={{ color: '#8C8B8B', marginLeft: 10, marginRight: 10, fontSize: 16 }} />
                                </InputAdornment>
                            ),
                            endAdornment: (
                                <Button position="end" onClick={() => setState({ ...state, searchVal: '', news: [] })}>
                                    <CloseIcon style={{ color: '#8C8B8B', marginLeft: 10, marginRight: 10, fontSize: 16 }} />
                                </Button>
                            ),
                            classes: { notchedOutline: classes.noBorder }
                        }}
                    />
                </Box>
                <Button onClick={() => setState({ ...state, showForm: true })} className={classes.createBtn}>
                    <AddIcon />create
                </Button>
            </Box>
            <TableContainer className={classes.container}>
                <Table stickyHeader aria-label="sticky table">
                    <TableHead>
                        <TableRow>
                            {columns.map((column) => (
                                <TableCell
                                    key={column.id}
                                    align={column.align}
                                    style={{ minWidth: column.minWidth }}
                                >
                                    {column.label}
                                </TableCell>
                            ))}
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {news.map((row) => {
                            return (
                                <Fragment>
                                    {
                                        !searchVal || (
                                            searchVal && (row.shortMessage.toLowerCase().indexOf(searchVal.toLowerCase()) !== -1 ||
                                                row.title.toLowerCase().indexOf(searchVal.toLowerCase()) !== -1
                                            )) ?
                                            <TableRow hover role="checkbox" tabIndex={-1} key={row.code}>
                                                {columns.map((column) => {
                                                    const value = column.label === 'Date' ? getDateTime(row['date'].date).date : row[column.id]
                                                    return (
                                                        <Fragment>
                                                            <TableCell key={column.id} align={column.align}>
                                                                {
                                                                    column.id === 'action' ?
                                                                        <Box className={classes.actionBtns}>
                                                                            {
                                                                                [row.Suspended ? 'Buyer' : 'Seller'].map(btn =>
                                                                                    <Button
                                                                                        className={classes[btn.toLowerCase() + 'Btn']}
                                                                                        onClick={() => {
                                                                                            setState({
                                                                                                ...state,
                                                                                                openPrompt: true,
                                                                                                selected: {
                                                                                                    _id: row._id
                                                                                                },
                                                                                                options: { title: 'Delete news', message: 'Are you sure you want to delete this news.' }
                                                                                            })
                                                                                        }}
                                                                                        key={btn}>Delete</Button>)
                                                                            }
                                                                        </Box>
                                                                        :
                                                                        column.label === 'Url' ?
                                                                            <a href={value} target="_blank">Link</a> :
                                                                            <span className={classes.value}>{column.format && typeof value === 'number' ? column.format(value) : value}</span>
                                                                }
                                                            </TableCell>

                                                        </Fragment>

                                                    );
                                                })}
                                            </TableRow> :
                                            null
                                    }
                                </Fragment>
                            );
                        })}
                    </TableBody>
                </Table>
            </TableContainer>
            {
                openPrompt ?
                    <Prompt
                        open={openPrompt}
                        propmpOption={propmpOption}
                        options={options}
                    /> :
                    null
            }
            {
                showForm ?
                    <Form title={'Create News'} open={showForm} onClose={() => setState({ ...state, showForm: false })} onSubmit={createNews} /> :
                    null
            }
        </Paper>
    )
}

export default Connect(mapStateToProps)(Index)
