import React, { useState, useEffect } from 'react';
import useStyles from '../Disputes/styles'
import { TableContainer, Table, TableHead, TableRow, TableCell, TableBody, Paper, Button } from '@material-ui/core';
import { Api } from '../../functions';
import { Connect, mapStateToProps } from '../../redux';
import Prompt from '../common/Prompt';
import Notify from "../Notifications";


function Index(props) {
    const classes = useStyles(),
        [state, setState] = useState({
            apps: null,
            openPrompt: false,
            options: {
                title: '',
                message: ''
            },
            selected: {
                status: '',
                _id: ''
            }
        }),
        { apps, openPrompt, selected, options } = state,
        { User } = props,
        columns = [
            { id: 'Name', label: 'Name', minWidth: 400 },
            { id: 'Users', label: 'Users', minWidth: 200 },
            {
                id: 'action',
                label: 'Action',
                sublabel: 'text',
                minWidth: 200,
                align: 'center'
            }
        ],

        propmpOption = (option) => {
            if (option === "Yes")
            Api({
                module: 'Apps',
                data: {
                    action: 'updateApp',
                    userId: User._id,
                    appId: selected._id,
                    status: selected.status
                }
            }).then(() => {
                getApps()
                Notify(`App has been ${selected.status} successfully.`, 'success')
            }).catch(() => Notify(`App could not be ${selected.status}.`, 'danger'))
        setState({ ...state, openPrompt: false, selected: {} });
        },

        getApps = () => {
            Api({
                module: 'Apps',
                data: {
                    action: 'fetchSupportApps',
                    userId: User._id,
                }
            }).then(res => {
                setState({ ...state, apps: res, openPrompt: false, selected: {} });
            }).catch(e => console.log(e))
        }

    useEffect(() => {
        getApps()
        return () => {

        }
    }, [])
    return (
        <Paper className={classes.root}>
            <TableContainer className={classes.container}>
                <Table stickyHeader aria-label="sticky table">
                    <TableHead>
                        <TableRow>
                            {columns.map((column) => (
                                <TableCell
                                    key={column.id}
                                    align={column.align}
                                    style={{ minWidth: column.minWidth }}
                                >
                                    {column.label}
                                </TableCell>
                            ))}
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {
                            apps?.map(app => (
                                <TableRow key={app._id}>
                                    <TableCell>
                                        {app.name}
                                    </TableCell>
                                    <TableCell>
                                        {app.users}
                                    </TableCell>
                                    <TableCell>
                                        {
                                            app?.status === "Pending" || app?.status === "Suspended" ? <Button className={classes.btnApproveApp} onClick={() => {
                                                setState({
                                                    ...state,
                                                    openPrompt: true,
                                                    selected: {
                                                        _id: app._id,
                                                        status: "Approved"
                                                    },
                                                    options: { title: 'Approve App', message: 'Are you sure you want to approve this app.' }
                                                })
                                            }

                                            }>Approve</Button> : <Button onClick={() => {
                                                setState({
                                                    ...state,
                                                    openPrompt: true,
                                                    selected: {
                                                        _id: app._id,
                                                        status: "Suspended"
                                                    },
                                                    options: { title: 'Suspend App', message: 'Are you sure you want to suspend this app.' }
                                                })
                                            }

                                            } className={classes.btnSuspendApp}>Suspend</Button>
                                        }
                                    </TableCell>
                                </TableRow>
                            ))
                        }
                    </TableBody>
                </Table>
            </TableContainer>
            {
                openPrompt ?
                    <Prompt
                        open={openPrompt}
                        propmpOption={propmpOption}
                        options={options}
                    /> : null
            }
        </Paper>
    )
}

export default Connect(mapStateToProps)(Index);
