import React, { useState, useEffect, Fragment } from 'react'
import { Api } from '../../functions'
import { Connect, mapStateToProps } from '../../redux'
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import useStyles from '../Disputes/styles'
import { Box, Button, InputAdornment, TextField } from '@material-ui/core';
import SearchIcon from '@material-ui/icons/Search'
import CloseIcon from '@material-ui/icons/Close';
import Prompt from '../common/Prompt';
import EditIcon from '@material-ui/icons/Edit';
import AgentsModal from './agentsModal';

const columns = [
    { id: 'Username', label: 'Username', minWidth: 100 },
    { id: 'total_USBD_release', label: 'USDB Received', minWidth: 100, align: 'center' },
    { id: 'total_USBD_balance', label: 'USDB Balance', minWidth: 100, align: 'center' },
    { id: 'total_USBD_sold', label: 'USDB Sales', minWidth: 100, align: 'center' },
    { id: 'total_USBD_bought', label: 'USDB Bought', minWidth: 100, align: 'center' },
    { id: 'net_USDB', label: 'USDB On sale', minWidth: 100, align: 'center' },
    { id: 'total_Ksh_sells', label: 'Revenue (Ksh)', minWidth: 100, align: 'center' },
    { id: 'total_Ksh_buys', label: 'Expenditure (Ksh)', minWidth: 100, align: 'center' },
    { id: 'net_Ksh', label: 'Balance (Ksh)', minWidth: 100, align: 'center' },
    // {
    //     id: 'country',
    //     label: 'Country',
    //     sublabel: 'text',
    //     minWidth: 270,
    //     align: 'left'
    // },
    // {
    //     id: 'action',
    //     label: 'Action',
    //     sublabel: 'text',
    //     minWidth: 120,
    //     align: 'center'
    // }
];

const Index = (props) => {
    const { User, history } = props,
        // history = useHistory(),
        [state, setState] = useState({
            searchVal: '',
            openPrompt: false,
            options: {
                title: '',
                message: ''
            },
            Users: [],
            distributions: [],
            records: [],
            selectedUser: {
                status: '',
                _id: ''
            },
            showList: false
        }),
        { Users, searchVal, showList, openPrompt, distributions, options, selectedUser, records } = state,
        getUser = (_id, field) => {
            let UserIdex = records.findIndex(q => q._id === _id);
            return UserIdex == -1 &&  field !== 'total_USBD_release'  ? 0 :
                field === 'total_USBD_release' ? _id === 0 ? records[UserIdex][field] : getUserDistribution(_id) :
                    field === 'total_USBD_balance' ? _id === 0 ? records[UserIdex][field] : getUserDistribution(_id) - records[UserIdex].net_USDB :
                        records[UserIdex][field]
        },
        getUserDistribution = (_id) => {
            let UserIdex = distributions.findIndex(q => q.ToId === _id);
            return distributions[UserIdex]?.Amount
        },
        fetchUsers = () => {
            Api({
                module: 'Support',
                data: {
                    action: 'getReserveAgents',
                    userId: User._id
                }
            }).then(({ users, records, distributions }) => {
                users = [...users, { _id: 0, Username: 'Total' }]
                records = [...records, {
                    _id: 0,
                    total_USBD_sold: records.map(a => a.total_USBD_sold).reduce((a, b) => a + b, 0),
                    total_USBD_release: distributions.map(a => a.Amount).reduce((a, b) => a + b, 0),
                    total_USBD_balance: distributions.map(a => a.Amount).reduce((a, b) => a + b, 0) - records.map(a => a.net_USDB).reduce((a, b) => a + b, 0),
                    net_USDB: records.map(a => a.net_USDB).reduce((a, b) => a + b, 0),
                    net_Ksh: records.map(a => a.net_Ksh).reduce((a, b) => a + b, 0),
                    total_USBD_bought: records.map(a => a.total_USBD_bought).reduce((a, b) => a + b, 0),
                    total_Ksh_sells: records.map(a => a.total_Ksh_sells).reduce((a, b) => a + b, 0),
                    total_Ksh_buys: records.map(a => a.total_Ksh_buys).reduce((a, b) => a + b, 0)
                }]
                setState({ ...state, Users: users, records, distributions })
            }).catch(e => console.log(e))
        },
        classes = useStyles(),
        propmpOption = (option) => {
            if (option === "Yes")
                Api({
                    module: 'Support',
                    data: {
                        action: 'changeAccountStatus',
                        userId: User._id,
                        accountId: selectedUser._id,
                        status: selectedUser.status ? false : true,
                        searchVal
                    }
                }).then(({ users, records }) => {
                    setState({ ...state, users, openPrompt: false, selectedUser: {} })
                })
            setState({ ...state, openPrompt: false, selectedUser: {} });
        }
    useEffect(() => {
        fetchUsers()
    }, [])
    return (
        <Paper className={classes.root}>
            <Box className={classes.headerView}>
                <Box className={classes.searchBox}>
                    <TextField
                        placeholder={'Search by username'}
                        className={classes.searchInput}
                        classes={{ notchedOutline: classes.input }}
                        onChange={(c) => setState({ ...state, searchVal: c.target.value })}
                        InputProps={{
                            disableUnderline: true,
                            startAdornment: (
                                <InputAdornment position="start">
                                    <SearchIcon style={{ color: '#8C8B8B', marginLeft: 10, marginRight: 10, fontSize: 16 }} />
                                </InputAdornment>
                            ),
                            endAdornment: (
                                <Button position="end" onClick={() => setState({ ...state, searchVal: '', users: [] })}>
                                    <CloseIcon style={{ color: '#8C8B8B', marginLeft: 10, marginRight: 10, fontSize: 16 }} />
                                </Button>
                            ),
                            classes: { notchedOutline: classes.noBorder }
                        }}
                    />
                </Box>
                <Button onClick={() => setState({ ...state, showList: true })} className={classes.createBtn}>
                    <EditIcon style={{ color: 'grey' }} /> Edit
                </Button>
            </Box>
            <TableContainer className={classes.container}>
                <Table stickyHeader aria-label="sticky table">
                    <TableHead>
                        <TableRow>
                            {columns.map((column) => (
                                <TableCell
                                    key={column.id}
                                    align={column.align}
                                    style={{ minWidth: column.minWidth }}
                                >
                                    {column.label}
                                </TableCell>
                            ))}
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {Users.map((row, i) => {
                            return (
                                <Fragment>
                                    {
                                        !searchVal || (
                                            searchVal && (row.Username.toLowerCase().indexOf(searchVal.toLowerCase()) !== -1)) ?
                                            <TableRow hover role="checkbox" tabIndex={-1} key={row.code}>
                                                {columns.map((column) => {
                                                    const value = column.label === 'Username' ?
                                                        row[column.id] :
                                                        getUser(row._id, column.id)
                                                    return (
                                                        <Fragment>
                                                            <TableCell key={column.id} align={column.align}>
                                                                {
                                                                    column.id === 'action' ?
                                                                        <Box className={classes.actionBtns}>
                                                                            {
                                                                                [row.Suspended ? 'Buyer' : 'Seller'].map(btn =>
                                                                                    <Button
                                                                                        className={classes[btn.toLowerCase() + 'Btn']}
                                                                                        onClick={() => {
                                                                                            setState({
                                                                                                ...state,
                                                                                                openPrompt: true,
                                                                                                selectedUser: {
                                                                                                    status: row.Suspended,
                                                                                                    _id: row._id
                                                                                                },
                                                                                                options: { title: 'Account status', message: 'Are you sure you want to ' + (row.Suspended ? 'reinstate' : 'suspend') + ' this account.' }
                                                                                            })
                                                                                        }}
                                                                                        key={btn}>{row.Suspended ? 'Unsuspend' : 'Suspend'}</Button>)
                                                                            }
                                                                        </Box>
                                                                        :
                                                                        <span style={{ fontWeight: i === Users.length - 1 ? 'bold' : 'normal' }} className={classes.value}>{!isNaN(value) ? value.toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",") : value}</span>
                                                                }
                                                            </TableCell>

                                                        </Fragment>

                                                    );
                                                })}
                                            </TableRow> :
                                            null
                                    }
                                </Fragment>
                            );
                        })}
                    </TableBody>
                </Table>
            </TableContainer>
            {
                openPrompt ?
                    <Prompt
                        open={openPrompt}
                        propmpOption={propmpOption}
                        options={options}
                    /> :
                    showList ?
                        <AgentsModal User={User} fetchUsers={fetchUsers} Users={Users} open={showList} onClose={() => setState({ ...state, showList: false })} /> :
                        null
            }
        </Paper>
    )
}

export default Connect(mapStateToProps)(Index)
