import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { TextField, makeStyles, Box } from '@material-ui/core';
const useStyles = makeStyles((theme) => ({
    actionBtns: {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'space-between'
    },
    btn: {
        textTransform: 'none'
    }
}));
export default function Prompt(props) {
    const classes = useStyles(),
        { open, message, onSubmit, onClose, selected } = props,
        buttons = ['Cancel', selected._id ? 'Update' : 'Create'],
        title = selected._id ? 'Update FAQ' : 'Create FAQ';
    return (
        <Dialog
            open={open}
            onClose={() => onClose()}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
        >
            <DialogTitle id="alert-dialog-title">{title}</DialogTitle>
            <DialogContent>
                <DialogContentText id="alert-dialog-description">{message}</DialogContentText>
                <form onSubmit={onSubmit} >
                    <TextField
                        variant="outlined"
                        margin="normal"
                        required
                        required
                        defaultValue={selected.Question}
                        fullWidth
                        name="Question"
                        label="Question"
                        id="Question"
                    />
                    <TextField
                        variant="outlined"
                        margin="normal"
                        required
                        defaultValue={selected.Answer}
                        fullWidth
                        id="Answer"
                        label="Answer"
                        name="Answer"
                        autoFocus
                        multiline={true}
                        rows={12}
                    />
                    <Box className={classes.actionBtns} >
                        {
                            buttons.map((button, index) => (
                                <Button
                                    type={index === 0 ? 'button' : 'submit'}
                                    className={classes.btn}
                                    key={index}
                                    onClick={() => {
                                        if (index === 0)
                                            onClose()
                                    }} color="primary"> {button} </Button>
                            ))
                        }
                    </Box>
                </form>
            </DialogContent>
        </Dialog>
    )
}