import { Box, Button, Typography } from '@material-ui/core'
import React from 'react';
import useStyles from './styles';
import userImage from '../../mages/avatar.jpg';
import LinesEllipsis from 'react-lines-ellipsis'
import { getDateTime } from '../../functions';
import BookmarkIcon from '@material-ui/icons/Bookmark';
import ImageIcon from '@material-ui/icons/Image';

const Item = (props) => {
    const classes = useStyles(),
        { user, userClicked, selectedUser, message, pinnedUsers } = props,
        { Image, Username, _id } = user || {},
        { text, time, unread, images } = message,
        messageDate = getDateTime(time),
        Time = messageDate.time,
        date = getDateTime().today === messageDate.transDate ? Time : getDateTime().yesterday === messageDate.transDate ? 'Yesterday' : messageDate.transDate
    return (
        <Button
            item
            className={classes[_id === selectedUser?._id ? 'listIemtSelect' : 'litstItem']}
            onClick={() => userClicked(user)}
        >
            <img alt={''} src={Image || userImage} className={classes.usersmallImg} />
            <Box className={classes.textUsername}>
                <Box className={classes.usernameTime}>
                    <Typography className={classes.username}>{Username || _id}</Typography>
                    <span className={classes.time}>{date} {unread ?
                        <Box className={classes.unread}>{unread}</Box> :
                        pinnedUsers.indexOf(user?._id) !== -1 ?
                            <Box>
                                <BookmarkIcon style={{ color: '#00A254', fontSize: 18 }} />
                            </Box> :
                            null
                    } </span>
                </Box>
                <div className={classes.textIcon}>
                    {
                        images?.length ?
                            <ImageIcon style={{ color: '#A9A9A9', fontSize: 18, marginRight: 5 }} />
                            : null
                    }
                    <LinesEllipsis
                        text={text}
                        maxLine={1}
                        ellipsis='...'
                        trimRight
                        basedOn='letters'
                        className={classes.text}
                    />
                </div>
            </Box>
        </Button>
    )
}
export default Item