import React, { useState, useEffect, useRef } from 'react';
import useStyles from './styles';
import { Api } from '../../functions';
import 'react-quill/dist/quill.snow.css';
import { Connect, mapStateToProps } from '../../redux';
import ReactQuill from 'react-quill';
import { Button, Dialog, DialogContent, DialogContentText, DialogTitle, TextField, Box } from '@material-ui/core';
 
function CreateBlog(props) {
    const classes = useStyles(),
        [state, setState] = useState({
            Body: '',
            Title: '',
            Icon: '',
            _id: null,
            error: null,
        }),
        { Body, Title, Icon, error, _id } = state,
        { open, User, onClose, blog } = props,
        handleChange = (name) => (e) => {
            const value = name === 'Body' ? e : e.target.value;
            setState({ ...state, [name]: value });
        },
        onSubmit = () => {
            if (Title.trim() && Icon.trim()) {
                Api({
                    module: "DevelopersBlog",
                    data: {
                        action: "createBlog",
                        userId: User._id,
                        Body:Body.replace(/<p><br><\/p>/g, ''),
                        Title,
                        Icon,
                        blogId: _id
                    }
                }).then(() => {
                    onClose()
                }).catch(err => {
                    onClose()
                    console.log(err)
                })
            } else {
                setState({ ...state, error: 'Please fill all details' })
            }
        }
    useEffect(() => {
        if (blog) {
            setState({ ...state, ...blog })
        }
        return () => {

        }
    }, [])
    return (
        <Dialog
            open={open}
            onClose={() => onClose()}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
        >
            <DialogTitle id="alert-dialog-title">Developers Blog</DialogTitle>
            <DialogContent>
                <DialogContentText id="alert-dialog-description"> </DialogContentText>
                <form >
                    <TextField
                        variant="outlined"
                        margin="normal"
                        required
                        value={Icon}
                        onChange={handleChange('Icon')}
                        fullWidth
                        label="Icon"
                        id="icon"
                        autoFocus
                    />
                    <TextField
                        variant="outlined"
                        margin="normal"
                        required
                        value={Title}
                        onChange={handleChange('Title')}
                        fullWidth
                        id="title"
                        label="Title"
                    />
                    <ReactQuill
                        modules={CreateBlog.modules}
                        formats={CreateBlog.formats}
                        bounds={'.quill'}
                        value={Body}
                        onChange={handleChange('Body')}
                        placeholder='Write something amazing...'
                    />
                    {error ? <div className={classes.errorLabel}>{error}</div> : null}
                    <Box className={classes.actionBtns}>
                        <Button className={classes.btn} onClick={() => {
                            onSubmit()
                        }} color="primary"> Submit </Button>
                    </Box>
                </form>
            </DialogContent>
        </Dialog>
    )
}


CreateBlog.modules = {
    toolbar: [
        [{ header: '1' }, { header: '2' }, { header: [3, 4, 5, 6] }, { font: [] }],
        [{ size: [] }],
        ['bold', 'italic', 'underline', 'strike', 'blockquote'],
        [{ list: 'ordered' }, { list: 'bullet' }],
        ['link', 'image', 'video'],
        ['clean'],
        ['code-block'],
    ],
};

CreateBlog.formats = [
    'header',
    'font',
    'size',
    'bold',
    'italic',
    'underline',
    'strike',
    'blockquote',
    'list',
    'bullet',
    'link',
    'image',
    'video',
    'code-block',
];

export default Connect(mapStateToProps)(CreateBlog)
