import React, { useEffect, useState } from 'react'
import { Api, getDateTime, numberWithCommas } from '../../functions'
import { Connect, mapStateToProps } from '../../redux'
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import useStyles from './styles'
import { Fragment } from 'react';
import { Box, Button, InputAdornment, TextField } from '@material-ui/core';
import SearchIcon from '@material-ui/icons/Search'
import CloseIcon from '@material-ui/icons/Close'
import Prompt from '../common/Prompt';
import Notify from "../Notifications";

const columns = [
    { id: '_id', label: 'Order Id', minWidth: 170 },
    { id: 'buyer', label: 'Buyer', minWidth: 220 },
    { id: 'seller', label: 'Seller', sublabel: 'date', minWidth: 220 },
    { id: 'amount', label: 'Amount  (USDB)', minWidth: 140, align: 'center' },
    // { id: 'type', label: 'Order type', minWidth: 140, align: 'center' },
    { id: 'currencyAmount', label: ' Order Currency', minWidth: 200 },
    {
        id: 'disputed',
        label: 'Dispute',
        sublabel: 'text',
        minWidth: 270,
        align: 'left'
    },
    {
        id: 'action',
        label: 'Action',
        sublabel: 'text',
        minWidth: 120,
        align: 'center'
    }
];

const Index = (props) => {
    const { User, history } = props,
        // history = useHistory(),
        [state, setState] = useState({
            disputes: [],
            searchVal: '',
            openPrompt: false,
            options: {
                title: '',
                message: ''
            }
        }),
        { disputes, searchVal, openPrompt, options, selected } = state,
        propmpOption = (option) => {
            if (option === "Yes")
                Api({
                    module: 'Support',
                    data: {
                        action: 'cancelOrder',
                        userId: User._id,
                        order: selected
                    }
                }).then(() => {
                    setState({ ...state, disputes: disputes.filter(a => a._id !== selected._id), openPrompt: false, selected: {} })
                    Notify('Order has been cancelled successfully.', 'success')
                }).catch(() => Notify('Order could not be cancelled.', 'danger'))
            setState({ ...state, openPrompt: false, selected: {} });
        },
        fetchDisputes = () => {
            Api({
                module: 'Support',
                data: {
                    action: 'fetchDisputes',
                    userId: User._id
                }
            }).then(disputes => setState({ ...state, disputes })).catch(e => console.log(e))
        },
        classes = useStyles()
    useEffect(() => {
        fetchDisputes()
        return () => {

        }
    }, [])
    return (
        <Paper className={classes.root}>
            <Box className={classes.searchBox}>
                <TextField
                    placeholder={'search by order id'}
                    className={classes.searchInput}
                    value={searchVal}
                    classes={{ notchedOutline: classes.input }}
                    onChange={(c) => setState({ ...state, searchVal: c.target.value })}
                    InputProps={{
                        disableUnderline: true,
                        startAdornment: (
                            <InputAdornment position="start">
                                <SearchIcon style={{ color: '#8C8B8B', marginLeft: 10, marginRight: 10, fontSize: 16 }} />
                            </InputAdornment>
                        ),
                        endAdornment: (
                            <Button position="end" onClick={() => setState({ ...state, searchVal: '' })}>
                                <CloseIcon style={{ color: '#8C8B8B', marginLeft: 10, marginRight: 10, fontSize: 16 }} />
                            </Button>
                        ),
                        classes: { notchedOutline: classes.noBorder }
                    }}
                />
            </Box>
            <TableContainer className={classes.container}>
                <Table stickyHeader aria-label="sticky table">
                    <TableHead>
                        <TableRow>
                            {columns.map((column) => (
                                <TableCell
                                    key={column.id}
                                    align={column.align}
                                    style={{ minWidth: column.minWidth }}
                                >
                                    {column.label}
                                </TableCell>
                            ))}
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {disputes.filter(a => (searchVal && a._id === searchVal) || !searchVal).map((row) => {
                            return (
                                <TableRow hover role="checkbox" tabIndex={-1} key={row.code}>
                                    {columns.map((column) => {
                                        const disputeTime = row.disputed?getDateTime(row.disputed.date).date + ' @ ' + getDateTime(row.disputed.date).time:'null',
                                            orderTime = getDateTime(row.date).date + ' @ ' + getDateTime(row.date).time,
                                            { currency, amount, price } = row.currencyAmount,
                                            value = column.id === 'disputed' && row.disputed ?
                                                (column.sublabel === 'date' ? disputeTime : row[column.id][column.sublabel]) :
                                                column.id === 'date' ? orderTime :
                                                    column.id === 'currencyAmount' ?
                                                        <Fragment>
                                                            <div className={classes.smallText}>Price: {currency + ' ' + numberWithCommas(price)}</div>
                                                            <div className={classes.smallText}>Amount: {currency + ' ' + numberWithCommas(amount)}</div>
                                                        </Fragment> :
                                                        column.id === 'buyer' || column.id === 'seller' ?
                                                            <Fragment>
                                                                {
                                                                    Object.keys(row[column.id]).filter(q => q !== '_id').map(key => (
                                                                        <div className={classes.smallText0}>{key}: {row[column.id][key]}</div>
                                                                    ))
                                                                }
                                                            </Fragment>
                                                            : row[column.id];
                                        return (
                                            <Fragment>


                                                <TableCell key={column.id} align={column.align}>
                                                    {
                                                        column.id === 'action' ?
                                                            <Box className={classes.actionBtns}>
                                                                {
                                                                    ['Buyer', 'Seller', 'Cancel'].map(btn =>
                                                                        <Button
                                                                            className={classes[btn.toLowerCase() + 'Btn']}
                                                                            onClick={() => {
                                                                                if (btn === 'Cancel')
                                                                                    setState({
                                                                                        ...state,
                                                                                        openPrompt: true,
                                                                                        selected: row,
                                                                                        options: { title: 'Cancel Order', message: 'Are you sure you want to cancel this order.' }
                                                                                    })
                                                                                else
                                                                                    history({ page: 'Chats', data: { userId: row[btn.toLowerCase()]._id, order: row } });
                                                                            }}
                                                                            key={btn}>{btn !== 'Cancel' ? 'Message' + btn : btn}</Button>)
                                                                }
                                                            </Box>
                                                            :
                                                            <span className={classes.value}>{column.format && typeof value === 'number' ? column.format(value) : value}</span>
                                                    }
                                                </TableCell>

                                            </Fragment>

                                        );
                                    })}
                                </TableRow>
                            );
                        })}
                    </TableBody>
                </Table>
            </TableContainer>
            {
                openPrompt ?
                    <Prompt
                        open={openPrompt}
                        propmpOption={propmpOption}
                        options={options}
                    /> :
                    null
            }
        </Paper>
    )
}

export default Connect(mapStateToProps)(Index)
