import React, { useEffect, useState, Fragment } from 'react';
import useStyles from './styles'
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, InputAdornment, Button, Box, TextField } from '@material-ui/core';
import { Search, Close, Add } from "@material-ui/icons";
import CreateBlog from './BlogForm';
import { Api, generateJWT } from '../../functions';
import Prompt from '../common/Prompt';
import Notify from "../Notifications";
import { Connect, mapStateToProps } from '../../redux';
import renderHTML from 'react-render-html';
import CodeBlock from './codeblock';
import ParamsTable from './tables';
function Index(props) {
    const [state, setState] = useState({
        searchVal: '',
        showForm: false,
        blogs: [],
        options: {
            title: '',
            message: ''
        },
        blog: null,
        openPrompt: false,
        selected: {
            status: '',
            _id: ''
        }
    }),
        { searchVal, showForm, blogs, blog, openPrompt, options, selected } = state,
        { User } = props,
        columns = [
            // { id: 'icon', label: 'Icon', minWidth: 170 },
            { id: 'title', label: 'Title', minWidth: 170 },
            { id: 'body', label: 'Body', minWidth: 200 },
            {
                id: 'action',
                label: 'Action',
                minWidth: 120,
                align: 'center'
            }
        ],
        btns = [
            { name: "Edit", action: (blog) => setState({ ...state, showForm: true, blog }) },
            {
                name: "Delete", action: (blog) => {
                    setState({
                        ...state, ...state, openPrompt: true,
                        selected: {
                            _id: blog._id
                        },
                        options: { title: 'Delete blog', message: 'Are you sure you want to delete this blog.' }
                    })
                }
            }
        ],
        onClose = () => {
            getBlogs()
        },
        classes = useStyles(),
        propmpOption = (option) => {
            if (option === "Yes")
                Api({
                    module: 'DevelopersBlog',
                    data: {
                        action: 'deleteBlog',
                        userId: User._id,
                        blogId: selected._id
                    }
                }).then(() => {
                    setState({ ...state, blogs: blogs.filter(a => a._id !== selected._id), openPrompt: false, selected: {} })
                    Notify('Blog has been deleted successfully.', 'success')
                }).catch(() => Notify('Blog could not be deleted.', 'danger'))
            setState({ ...state, openPrompt: false, selected: {} });
        },
        getBlogs = () => {
            Api({
                module: "DevelopersBlog",
                data: {
                    action: "getBlogs",
                    userId: User._id
                }
            }).then(blogs => {
                setState({ ...state, showForm: false, blogs, blog: null })
            }).catch(err => console.log(err))
        },
        renderCodeText = (body) => {
            /**
             * <CodeBlock
            code={body}
            language={'javascript'}
            showLineNumbers={true}
            startingLineNumber={1}
        /> 
             */
            let code = body.split('<pre class="ql-syntax" spellcheck="false">')
            return code
        },
        returnTable = (data) => {
            return data
        }
    useEffect(() => {
        getBlogs()
        generateJWT().then(resp => {
            // alert(JSON.stringify(resp))
        }).catch(e => alert(e))
        return () => {

        }
    }, [])
    return (
        <Paper className={classes.root}>
            <Box className={classes.headerView}>
                <Box className={classes.searchBox}>
                    <TextField
                        placeholder={'search by blog id'}
                        className={classes.searchInput}
                        value={searchVal}
                        classes={{ notchedOutline: classes.input }}
                        onChange={(c) => setState({ ...state, searchVal: c.target.value })}
                        InputProps={{
                            disableUnderline: true,
                            startAdornment: (
                                <InputAdornment position="start">
                                    <Search style={{ color: '#8C8B8B', marginLeft: 10, marginRight: 10, fontSize: 16 }} />
                                </InputAdornment>
                            ),
                            endAdornment: (
                                <Button position="end" onClick={() => setState({ ...state, searchVal: '' })}>
                                    <Close style={{ color: '#8C8B8B', marginLeft: 10, marginRight: 10, fontSize: 16 }} />
                                </Button>
                            ),
                            classes: { notchedOutline: classes.noBorder }
                        }}
                    />
                </Box>
                <Button onClick={() => setState({ ...state, showForm: true })} className={classes.createBtn}>
                    <Add style={{ color: 'grey' }} /> Create
                </Button>
            </Box>
            <TableContainer className={classes.container}>
                <Table stickyHeader aria-label="sticky table">
                    <TableHead>
                        <TableRow>
                            {columns.map(column => (
                                <TableCell
                                    key={column.id}
                                    style={{ minWidth: column.minWidth }}>
                                    {column.label}
                                </TableCell>
                            ))}
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {blogs.map(blog => (
                            !searchVal || (
                                searchVal && (blog.Icon.toLowerCase().indexOf(searchVal.toLowerCase()) !== -1 ||
                                    blog.Title.toLowerCase().indexOf(searchVal.toLowerCase()) !== -1 ||
                                    blog.Body.toLowerCase().indexOf(searchVal.toLowerCase()) !== -1
                                )) ?
                                <TableRow hover role="checkbox" tabIndex={-1} key={blog._id}>
                                    {
                                        columns.map((column) => {
                                            return (
                                                <Fragment>
                                                    <TableCell key={column.id}>
                                                        {column.id === 'action' ? <Box className={classes.actionBtns}>
                                                            {btns.map(btn => (
                                                                <Button
                                                                    className={classes[btn.name.toLowerCase() + 'Btn']}
                                                                    onClick={() => btn.action(blog)}
                                                                    key={btn.name}>{btn.name}</Button>
                                                            ))}
                                                        </Box> : column.id === 'body' ?
                                                            <div>
                                                                {
                                                                    renderCodeText(blog.Body).map(a =>
                                                                        a.includes('</pre>') ? (
                                                                            a.split('</pre>').map((b, i) =>
                                                                                i === 0 ?
                                                                                    (
                                                                                        b.includes('addTable') ?
                                                                                            <ParamsTable data={b} />
                                                                                            :
                                                                                            <CodeBlock
                                                                                                code={b.replace(/&lt;/g, '<').replace(/&gt;/g, '>')}
                                                                                                language={'javascript'}
                                                                                                showLineNumbers={false}
                                                                                            // startingLineNumber={1}
                                                                                            />
                                                                                    ) : renderHTML(b)
                                                                            )
                                                                        )
                                                                            : renderHTML(a)
                                                                    )
                                                                }
                                                            </div>
                                                            : <span className={classes.value}>{blog[column.label]}</span>}
                                                    </TableCell>
                                                </Fragment>
                                            )
                                        })
                                    }

                                </TableRow> : null
                        ))}

                    </TableBody>
                </Table>
            </TableContainer>
            {showForm ? <CreateBlog onClose={onClose} open={showForm} blog={blog} /> : null}
            {openPrompt ? <Prompt open={openPrompt}
                propmpOption={propmpOption}
                options={options} /> : null}
        </Paper>
    )
}

export default Connect(mapStateToProps)(Index);
