import React, { useState } from 'react'
import { Api, getDateTime, numberWithCommas } from '../../functions'
import { Connect, mapStateToProps } from '../../redux'
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import useStyles from '../Disputes/styles'
import { Fragment } from 'react';
import { Box, Button, InputAdornment, TextField } from '@material-ui/core';
import SearchIcon from '@material-ui/icons/Search'
import CloseIcon from '@material-ui/icons/Close';
import Prompt from '../common/Prompt';
const columns = [
    { id: '_id', label: 'User Id', minWidth: 170 },
    { id: 'Username', label: 'Username', minWidth: 170 },
    { id: 'Email', label: 'Email', sublabel: 'date', minWidth: 170 },
    {
        id: 'country',
        label: 'Country',
        sublabel: 'text',
        minWidth: 270,
        align: 'left'
    },
    {
        id: 'action',
        label: 'Action',
        sublabel: 'text',
        minWidth: 120,
        align: 'center'
    }
];

const Index = (props) => {
    const { User, history } = props,
        // history = useHistory(),
        [state, setState] = useState({
            users: [],
            searchVal: '',
            openPrompt: false,
            options: {
                title: '',
                message: ''
            },
            selectedUser: {
                status: '',
                _id: ''
            }
        }),
        { users, searchVal, openPrompt, options, selectedUser } = state,
        fetchUsers = (text) => {
            Api({
                module: 'Support',
                data: {
                    action: 'searchUser',
                    userId: User._id,
                    searchVal: text
                }
            }).then(users => setState({ ...state, users, searchVal: text })).catch(e => console.log(e))
        },
        classes = useStyles(),
        propmpOption = (option) => {
            if (option === "Yes")
                Api({
                    module: 'Support',
                    data: {
                        action: 'changeAccountStatus',
                        userId: User._id,
                        accountId: selectedUser._id,
                        status: selectedUser.status ? false : true,
                        searchVal
                    }
                }).then(users => {
                    setState({ ...state, users, openPrompt: false, selectedUser: {} })
                })
            setState({ ...state, openPrompt: false, selectedUser: {} });
        }
    return (
        <Paper className={classes.root}>
            <Box className={classes.searchBox}>
                <TextField
                    placeholder={'Search by email or username'}
                    className={classes.searchInput}
                    classes={{ notchedOutline: classes.input }}
                    onChange={(c) => {
                        let val = c.target.value
                        if (val.trim()) {
                            setState({ ...state, searchVal: val })
                            fetchUsers(val)
                        }
                        else
                            setState({ ...state, searchVal: val, users: [] })

                    }}
                    InputProps={{
                        disableUnderline: true,
                        startAdornment: (
                            <InputAdornment position="start">
                                <SearchIcon style={{ color: '#8C8B8B', marginLeft: 10, marginRight: 10, fontSize: 16 }} />
                            </InputAdornment>
                        ),
                        endAdornment: (
                            <Button position="end" onClick={() => setState({ ...state, searchVal: '', users: [] })}>
                                <CloseIcon style={{ color: '#8C8B8B', marginLeft: 10, marginRight: 10, fontSize: 16 }} />
                            </Button>
                        ),
                        classes: { notchedOutline: classes.noBorder }
                    }}
                />
            </Box>
            <TableContainer className={classes.container}>
                <Table stickyHeader aria-label="sticky table">
                    <TableHead>
                        <TableRow>
                            {columns.map((column) => (
                                <TableCell
                                    key={column.id}
                                    align={column.align}
                                    style={{ minWidth: column.minWidth }}
                                >
                                    {column.label}
                                </TableCell>
                            ))}
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {users.map((row) => {
                            return (
                                <TableRow hover role="checkbox" tabIndex={-1} key={row.code}>
                                    {columns.map((column) => {
                                        const value = column.label === 'Country' ? row['country'].name : row[column.id]
                                        return (
                                            <Fragment>
                                                <TableCell key={column.id} align={column.align}>
                                                    {
                                                        column.id === 'action' ?
                                                            <Box className={classes.actionBtns}>
                                                                {
                                                                    [row.Suspended ? 'Buyer' : 'Seller'].map(btn =>
                                                                        <Button
                                                                            className={classes[btn.toLowerCase() + 'Btn']}
                                                                            onClick={() => {
                                                                                setState({
                                                                                    ...state,
                                                                                    openPrompt: true,
                                                                                    selectedUser: {
                                                                                        status: row.Suspended,
                                                                                        _id: row._id
                                                                                    },
                                                                                    options: { title: 'Account status', message: 'Are you sure you want to ' + (row.Suspended ? 'reinstate' : 'suspend') + ' this account.' }
                                                                                })
                                                                            }}
                                                                            key={btn}>{row.Suspended ? 'Unsuspend' : 'Suspend'}</Button>)
                                                                }
                                                            </Box>
                                                            :
                                                            <span className={classes.value}>{column.format && typeof value === 'number' ? column.format(value) : value}</span>
                                                    }
                                                </TableCell>

                                            </Fragment>

                                        );
                                    })}
                                </TableRow>
                            );
                        })}
                    </TableBody>
                </Table>
            </TableContainer>
            {
                openPrompt ?
                    <Prompt
                        open={openPrompt}
                        propmpOption={propmpOption}
                        options={options}
                    /> :
                    null
            }
        </Paper>
    )
}

export default Connect(mapStateToProps)(Index)
