import { Box } from '@material-ui/core'
import React, { useState } from 'react';
import { getDateTime } from '../../functions';
import useStyles from './styles';
import ImgsViewer from "react-images-viewer";
const Message = (props) => {
    const classes = useStyles(),
        [state, setState] = useState({
            isOpen: false,
            currImg: ''
        }),
        { user, message, datam } = props,
        { text, to, time, images } = message,
        { indexPlus } = datam,
        messDate = getDateTime(time),
        messPlusDate = indexPlus ? getDateTime(indexPlus?.time) : {},
        messDay = getDateTime().today === messDate.transDate ? 'Today' : getDateTime().yesterday === messDate.transDate ? 'Yesterday' : messDate.date,
        messDayPlus = getDateTime().today === messPlusDate.transDate ? 'Today' : getDateTime().yesterday === messPlusDate.transDate ? 'Yesterday' : messPlusDate.date,
        { isOpen, currImg } = state,
        fromUser = user?._id === to,
        IMG_SET = images?.map(a => ({
            src: a,
            caption: text
        })),
        goToImg = (indexInc) => {
            setState({ ...state, currImg: currImg + indexInc })
        }


    return (
        <div>
            {!messDayPlus || messDay !== messDayPlus ? <div className={classes.date}>{messDay}</div> : null}
            <Box className={classes.messageItem} style={{ justifyContent: fromUser ? 'flex-end' : 'flex-start' }}>
                <Box className={classes.messageSpan} style={{ backgroundColor: fromUser ? '#FCE1B9' : '#EDEDED' }}>
                    {
                        images?.length ?
                            <div className={classes.imagesView}>
                                {
                                    images.map((a, i) => (
                                        <img alt={''} onClick={() => setState({ ...state, isOpen: true, currImg: i })}
                                            key={a}
                                            src={a}
                                            className={classes.messageImg} />
                                    ))
                                }
                                <br />
                            </div>
                            :
                            null
                    }
                    <span className={classes.MessageText}>{text}</span><br />
                    <span className={classes.MessTime} >{messDate.time}</span>
                </Box>
                <ImgsViewer
                    preloadNextImg={images ? true : false}
                    imgs={IMG_SET}
                    currImg={currImg}
                    onClickPrev={() => goToImg(-1)}
                    onClickNext={() => goToImg(1)}
                    onClose={() => setState({ ...state, isOpen: false })}
                    isOpen={isOpen}
                />
            </Box>
        </div>
    )
}
export default Message