import React, { Fragment, Component } from 'react';
import { Box, Button, Grid, Hidden, Toolbar, Typography } from '@material-ui/core';
import { useStyles } from './styles0';
import TextareaAutosize from 'react-textarea-autosize';
import { Api, getDateTime, instantMessages, updateJWTToken } from '../../functions';
import Item from './item';
import Message from './message';
import ChatBubble from '@material-ui/icons/ChatBubble';
import userImage from '../../mages/avatar.jpg'
import { parsePhoneNumber } from 'libphonenumber-js';
import { Connect, mapStateToProps } from '../../redux';
import ReconnectingWebSocket from 'reconnecting-websocket';
import { withStyles } from '@material-ui/core/styles';
import ScrollableFeed from 'react-scrollable-feed'
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
const socketUrl = process.env.REACT_APP_socketUrl,
    ws =  new WebSocket("wss://benkiko.africa", "protocolOne"),
    // new ReconnectingWebSocket(socketUrl, [], {
    //     constructor: WebSocket,
    //     connectionTimeout: 5000,  // in milliseconds
    //     reconnectInterval: 1000,
    
    // }),
    sendData = (data) => ws.send(JSON.stringify(data));
class Chat extends Component {
    constructor(props) {
        super(props)
        this.state = {
            users: [],
            chats: [],
            selectedUser: {},
            messages: [],
            text: '',
            pinned: false,
            pinnedUsers: [],
            anchorEl: null,
            sentMessage: false
        }
        this.messagesEndRef = ''

    }
    scrollToBottom = () => {
        if (this.messagesEnd) {
            this.messagesEnd.current.scrollIntoView({ behavior: 'smooth' })
        }
    }
    getConversations() {
        const { User, data } = this.props,
            { sentMessage } = this.state;
        updateJWTToken(User.token);
        Api({
            module: 'Chat',
            data: {
                action: 'getSupportConversations',
                userId: User._id,
                chatFor: data?.userId
            }
        }).then(({ users, chats, pinnedUsers }) => {
            if (data?.userId) {
                let chatIndex = chats.findIndex(q => q.chatFor === data.userId),
                    userIndex = users.findIndex(q => q._id === data.userId)
                this.setState({ users, chats: chatIndex === -1 ? [{ chatFor: data.userId }, ...chats] : chats, pinnedUsers, sentMessage: true })
                if (!sentMessage)
                    this.userClicked(users[userIndex])
            }
            else
                this.setState({ users, chats, pinnedUsers })
        }).catch(e => console.log(e))
    }
    componentDidUpdate() {
        this.scrollToBottom();
    }

    componentDidMount() {
        const { User } = this.props;
        this.getConversations()
        ws.onopen = (e) => {
            sendData({
                action: 'saveUserOnline',
                userId: User._id,
                support: true
            });
            this.getConversations();
        }
        ws.onerror = (e) => {
            console.log(e.message)
        }
        ws.onclose = (e) => {

        };
        ws.onmessage = (e) => {
            var data = JSON.parse(e.data),
                { action, message } = data;
            switch (action) {
                case 'newMessage':
                    const { selectedUser, messages } = this.state,
                        { from } = message;
                    if (from === selectedUser._id) {
                        var Messages = [...this.state.messages];
                        Messages[messages.length] = message
                        this.setState({ messages: Messages });
                    }
                    this.getConversations()
                    break;
                default:
                    break;
            }
        }
        this.scrollToBottom();
        setInterval(() => sendData({
            action: 'saveUserOnline',
            userId: User._id,
            support: true
        }), 10000)
    }
    userClicked = (selectedUser) => {
        const { User } = this.props
        this.setState({ selectedUser, messages: [] })
        Api({
            module: 'Chat',
            data: {
                action: 'getChats',
                chatFor: selectedUser._id,
                userId: User._id,
                fromSupport: true
            }
        }).then(({ messages, pinned }) => {
            this.setState({ selectedUser, messages, pinned })
            // this.getConversations()
        }).catch(e => console.log(e))
    }
    render() {
        const { classes, User, data } = this.props,
            { users, selectedUser, chats, messages, text, pinned, pinnedUsers, anchorEl } = this.state,
            details = [
                { label: 'Email', key: 'Email' },
                { label: 'Phone', key: 'Phone' },
                { label: 'country', key: 'Country' },
                { label: 'Date Joined', key: 'DateRegistered' }
            ],
            getUser = (userId, Users) => {
                var index = Users.findIndex(q => q?._id === userId)
                return users[index]
            },
            sendMessage = () => {
                let message = {
                    from: User._id,
                    to: selectedUser._id,
                    support: true,
                    text,
                    time: Date.now(),
                    chatFor: selectedUser._id,
                    userId: User._id,
                    fromSupport:true
                }
                sendData({
                    module: 'Chat',
                    data: {
                        action: 'newMessage',
                        userId: User._id,
                        message
                    }
                })
                this.setState({ text: '', messages: [...this.state.messages, message] })
                this.getConversations()
            },
            showPopover = (event) => {
                this.setState({ anchorEl: event.currentTarget })
            },
            handleClose = (Answer) => {
                this.setState({ anchorEl: null, text: Answer || text })
            };


        return (
            <Grid className={classes.root}>
                <Grid xs={5} sm={6} md={4} lg={3} className={classes.listRoot}>
                    <Toolbar className={classes.toolbar}>
                        <Typography className={classes.title} >All Conversations</Typography>
                    </Toolbar>
                    <Grid className={classes.userList}>
                        {
                            chats.map((item) => (
                                <Item
                                    key={item._id}
                                    user={getUser(item.chatFor, users)}
                                    userClicked={this.userClicked}
                                    selectedUser={selectedUser}
                                    message={item}
                                    pinnedUsers={pinnedUsers}
                                />
                            ))
                        }
                    </Grid>
                </Grid>
                <Grid xs={7} sm={6} md={8} lg={9} className={classes.chatRoot}>
                    {
                        selectedUser?._id ?
                            <Fragment>
                                <Toolbar className={classes.toolbar}>
                                    <img alt={''} src={selectedUser.Image || userImage} className={classes.usersmallImg} />
                                    <Typography className={classes.userTitle} >{selectedUser.Username || selectedUser._id}</Typography>
                                </Toolbar>
                                <Grid className={classes.chatBox}>
                                    <Grid xs={12} sm={12} md={12} lg={8} xl={9} className={classes.ChatBox}>
                                        <Grid className={classes.Chats}
                                            ref={this.messagesEndRef}
                                        >
                                            <ScrollableFeed className={classes.autoScroll}>
                                                {
                                                    messages/*.sort((a, b) => a.time < b.time ? -1 : a.time > b.time ? 1 : 0)*/.map((message, index) => {
                                                        const curIndex = index,
                                                            indexPlus = messages[curIndex - 1],
                                                            currentIndex = messages[curIndex],
                                                            sameChat = indexPlus && indexPlus.from === currentIndex.from,
                                                            datam = {
                                                                indexPlus, sameChat
                                                            }
                                                        return (<Message datam={datam} message={message} user={getUser(selectedUser._id, users)} />)
                                                    }
                                                    )
                                                }
                                            </ScrollableFeed>
                                        </Grid>
                                        {
                                            !pinned ?
                                                <Grid className={classes.chatfooter}>
                                                    <Box className={classes.textArea}>
                                                        <Menu
                                                            id="simple-menu"
                                                            anchorEl={anchorEl}
                                                            keepMounted
                                                            open={Boolean(anchorEl)}
                                                            onClose={() => handleClose()}
                                                        >
                                                            {
                                                                instantMessages({ User, order: data?.order || {} }).map(({ Title, Answer }) => <MenuItem key={Title} onClick={() => handleClose(Answer)}>{Title}</MenuItem>)
                                                            }
                                                        </Menu>
                                                        <TextareaAutosize
                                                            placeholder={'Type message'}
                                                            maxRows={7}
                                                            value={text}
                                                            onChange={c => {
                                                                let text = c.target.value;
                                                                if (text.length === 1 && text === '/')
                                                                    showPopover(c)
                                                                else
                                                                    this.setState({ text })
                                                            }}
                                                            minRows={2}
                                                            className={classes.input}
                                                        />
                                                        <Button
                                                            onClick={sendMessage}
                                                            className={classes.sendBtn}
                                                            disabled={!text.trim()}
                                                        >Send</Button>
                                                    </Box>
                                                </Grid> :
                                                null
                                        }
                                    </Grid>
                                    <Hidden mdDown >
                                        <Box className={classes.userDetails}>
                                            <img alt={''}  src={selectedUser.Image || userImage} className={classes.userBigImage} />
                                            <Typography className={classes.userTitle0} >{selectedUser.Username || selectedUser._id}</Typography>
                                            {
                                                details.map(({ label, key }) => (
                                                    <Box key={label} className={classes.detail}>
                                                        <Typography className={classes.label}>{label}</Typography>
                                                        {/* parsePhoneNumber(selectedUser.Phone).format('INTERNATIONAL') */}
                                                        <Typography className={classes.value}>{label === 'country' ? (selectedUser.country?.emoji + ' ' + selectedUser.country?.name) : key === 'DateRegistered' ? getDateTime(selectedUser.DateRegistered).date : label === 'Phone' && selectedUser.Phone ? selectedUser.Phone : selectedUser[key] || 'Not Provided'}</Typography>
                                                    </Box>
                                                ))
                                            }
                                        </Box>
                                    </Hidden>
                                </Grid>
                            </Fragment> :
                            <Box className={classes.emptyView}>
                                <ChatBubble style={{ color: '#A5A5A5' }} />
                                <Typography className={classes.emptyText}>Start Conversation</Typography>
                            </Box>
                    }
                </Grid>
            </Grid>
        )
    }
}
export default Connect(mapStateToProps)(withStyles(useStyles)(Chat))