import React from 'react';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import ListSubheader from '@material-ui/core/ListSubheader';
import DashboardIcon from '@material-ui/icons/Dashboard';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import PeopleIcon from '@material-ui/icons/People';
import PeopleOutIcon from '@material-ui/icons/PeopleOutline';
import WifiIcon from '@material-ui/icons/Wifi';
import QuestionAnswerIcon from '@material-ui/icons/QuestionAnswer';
import BarChartIcon from '@material-ui/icons/BarChart';
import LayersIcon from '@material-ui/icons/Layers';
import LogOutIcon from '@material-ui/icons/ExitToApp';
import PersonIcon from '@material-ui/icons/PersonAdd';
import AssignmentIcon from '@material-ui/icons/Assignment';
import CallSplitIcon from '@material-ui/icons/CallSplit';
import ChatBubbleIcon from '@material-ui/icons/ChatBubble';
import Book from "@material-ui/icons/Book";
import { makeStyles } from '@material-ui/core/styles';
import { Badge } from '@material-ui/core';
import TransferWithinAStationIcon from '@material-ui/icons/TransferWithinAStation';
import { Apps } from '@material-ui/icons';
const links = [
    { text: 'Dashboard', icon: <DashboardIcon /> },
    { text: 'Disputes', icon: <CallSplitIcon /> },
    { text: 'Chats', icon: <ChatBubbleIcon /> },
    { text: 'Apps', icon: <Apps /> },
    { text: 'Orders', icon: <ShoppingCartIcon /> },
    { text: 'Customers', icon: <PeopleIcon /> },
    { text: 'Users', icon: <PeopleOutIcon /> },
    { text: 'Developer Blogs', icon: <Book /> },
    { text: 'Reserve Agents', icon: <TransferWithinAStationIcon /> },
    { text: 'News', icon: <WifiIcon /> },
    { text: 'FAQs', icon: <QuestionAnswerIcon /> },
    { text: 'Reports', icon: <BarChartIcon /> },
    { text: 'Integrations', icon: <LayersIcon /> }
],
    useStyles = makeStyles((theme) => ({
        listItem: {
            backgroundColor: '#D6D6D6'
        }
    }))
export const MainListItems = ({ listSelect, onClick, notes }) => {
    const classes = useStyles();
    return (<div>
        {
            links.map(({ icon, text }, index) => (
                <ListItem
                    onClick={() => onClick({ page: text, route: 'Charts' })}
                    button key={index}
                    className={classes[listSelect === text ? 'listItem' : '']}>
                    <ListItemIcon>
                        {
                            notes[text] ?
                                <Badge badgeContent={notes[text] > 20 ? '20+' : notes[text]} color="secondary">
                                    {icon}
                                </Badge> :
                                icon
                        }
                    </ListItemIcon>
                    <ListItemText primary={text} />
                </ListItem>
            ))
        }
    </div>)
}

export const secondaryListItems = (onClick) => {
    return (
        <div>
            <ListItem button onClick={() => onClick('SignUp')} >
                <ListItemIcon>
                    <PersonIcon />
                </ListItemIcon>
                <ListItemText primary="Register Agent" />
            </ListItem>
            <ListItem button onClick={() => onClick('LogOut')}>
                <ListItemIcon>
                    <LogOutIcon />
                </ListItemIcon>
                <ListItemText primary="Log Out" />
            </ListItem>
            <ListSubheader inset>Saved reports</ListSubheader>
            <ListItem button>
                <ListItemIcon>
                    <AssignmentIcon />
                </ListItemIcon>
                <ListItemText primary="Current month" />
            </ListItem>
            <ListItem button>
                <ListItemIcon>
                    <AssignmentIcon />
                </ListItemIcon>
                <ListItemText primary="Last quarter" />
            </ListItem>
            <ListItem button>
                <ListItemIcon>
                    <AssignmentIcon />
                </ListItemIcon>
                <ListItemText primary="Year-end sale" />
            </ListItem>
        </div>
    )
};
