import React, { useState, useEffect, useRef } from 'react';
import clsx from 'clsx';
import CssBaseline from '@material-ui/core/CssBaseline';
import Drawer from '@material-ui/core/Drawer';
import Box from '@material-ui/core/Box';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import { MainListItems, secondaryListItems } from './listItems';
import logo from '../../mages/logoCard.png'
import Chart from './Chart';
import Orders from './Orders';
import Chat from '../Chat'
import Disputes from '../Disputes'
import { Connect, mapDispatchToProps, mapStateToProps } from '../../redux';
import { useHistory } from 'react-router-dom';
import { useLocation } from "react-router-dom";
import { Badge } from '@material-ui/core';
import NotificationsIcon from '@material-ui/icons/Notifications'
import useStyles from './styles'
import Copyright from '../common/copyright'
import { Api, updateJWTToken } from '../../functions';
import Customers from './Customers';
import DeveloperBlogs from '../DeveloperBlog';
import Apps from '../Apps';
import Users from '../users';
import ReserveAgents from '../users/reserverAgents';
import News from '../News';
import FAQs from '../Faqs'
const Dashboard = (props) => {
  const { setByField, notes, User } = props,
    history = useHistory(),
    [state, setState] = useState({
      listSelect: 'Dashboard',
      data: ''
    }),
    location = useLocation(),
    timer = useRef(),
    { listSelect, data } = state,
    classes = useStyles(),
    ListItemClick = ({ page, route, data }) => {
      setState({ ...state, listSelect: route, data })
      history.push("/", { page });

    },
    [open, setOpen] = React.useState(true),
    pages = {
      Dashboard: <Chart data={data} />,
      Disputes: <Disputes history={ListItemClick} />,
      Chats: <Chat data={data} />,
      Orders: <Orders />,
      Apps: <Apps/>,
      Customers: <Customers />,
      'Developer Blogs': <DeveloperBlogs/>,
      Users: <Users />,
      'Reserve Agents': <ReserveAgents />,
      News: <News />,
      FAQs: <FAQs />,
      Reports: <Chart />,
      Integrations: <Chart />
    },
    handleDrawerOpen = () => {
      setOpen(true);
    },
    handleDrawerClose = () => {
      setOpen(false);
    },
    onListItemPress = (clicked) => {
      if (clicked === 'LogOut')
        setByField('User', '')
      else if (clicked === 'SignUp')
        history.push("/register");
    },

    totalNotes = Object.values(notes).reduce((a, b) => a + b, 0),
    fetchSupportActivities = () => {
      updateJWTToken(User.token);
      Api({
        module: 'Support',
        data: {
          userId: User._id,
          action: 'fetchActivities'
        }
      }).then(res => {
        setByField('notes', res)
      }).catch(e => console.log(e))
    },
    timerCount = () => {
      timer.current = setInterval(() => {
        fetchSupportActivities()
      }, 10000)
      fetchSupportActivities();
    }
  useEffect(() => {
    setState({ ...state, listSelect: location.state?.page });
    timerCount()
    return () => {
      clearInterval(timer.current);

    }
  }, [listSelect])
  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar position="absolute" className={clsx(classes.appBar, open && classes.appBarShift)}>
        <Toolbar className={classes.toolbar}>
          <IconButton
            edge="start"
            color="inherit"
            aria-label="open drawer"
            onClick={handleDrawerOpen}
            className={clsx(classes.menuButton, open && classes.menuButtonHidden)}
          >
            <MenuIcon />
          </IconButton>
          <Box className={classes.LogoBox}>
            <img src={logo} className={classes.logo} />
            <Typography component="h1" variant="h6" color="inherit" noWrap className={classes.title}>
              Benkiko
            </Typography>
          </Box>
          <IconButton color="inherit">
            <Badge badgeContent={totalNotes > 20 ? '20+' : totalNotes} color="secondary">
              <NotificationsIcon />
            </Badge>
          </IconButton>
        </Toolbar>
      </AppBar>
      <Drawer
        variant="permanent"
        classes={{
          paper: clsx(classes.drawerPaper, !open && classes.drawerPaperClose),
        }}
        open={open}
      >
        <div className={classes.toolbarIcon}>
          <IconButton onClick={handleDrawerClose}>
            <ChevronLeftIcon />
          </IconButton>
        </div>
        <Divider />
        <List>{MainListItems({ listSelect, onClick: ListItemClick, notes })}</List>
        <Divider />
        <List>{secondaryListItems(onListItemPress)}</List>
      </Drawer>
      <main className={classes.content}>
        <div className={classes.appBarSpacer} />
        <Grid item xs={10} className={classes.container} >
          {/* <Grid container spacing={10}> */}
          {/* Chart */}
          {/* <Grid item xs={12} md={8} lg={9}>
              <Paper className={fixedHeightPaper}>
                <Chart />
              </Paper>
            </Grid> */}
          {/* Recent Deposits */}
          {/* <Grid item xs={12} md={4} lg={3}>
              <Paper className={fixedHeightPaper}>
                <Deposits />
              </Paper>
            </Grid> */}
          {/* Recent Orders */}
          <Grid item >
            <Paper className={classes.paper}>
              {pages[listSelect]}
            </Paper>
          </Grid>
          {/* </Grid> */}
          <Box pt={4}>
            <Copyright />
          </Box>
        </Grid>
      </main>
    </div>
  );
}
export default Connect(mapStateToProps, mapDispatchToProps)(Dashboard)